iBAAA,CADA,2CAlDF,CAqDE,qCAEE,wBAAA,CADA,iCAlDJ,CAsDE,sCAEE,wBCrEW,CDyEX,iBAAA,CAHA,aCrES,CD4ET,cAAA,CANA,eAAA,CAOA,gBAAA,CAJA,eAAA,CANA,iBAAA,CAQA,SAAA,CADA,OAlDJ,CAwDE,mDACE,eAAA,CAIA,2BAAA,CADA,YAAA,CADA,QAAA,CADA,SAnDJ,CAwDI,uDAOE,2BAAA,CAHA,WAAA,CAFA,QAAA,CAGA,mBAAA,CAAA,gBAAA,CACA,yBAAA,CAAA,sBAAA,CALA,SAAA,CAEA,UAlDN,CEfI,yBF0EA,sCAGE,iBAAA,CACA,cAAA,CACA,gBAAA,CACA,eAAA,CAJA,UAAA,CADA,QAnDJ,CA2DE,mDACE,YAzDJ,CACF,CA4DE,kDACE,WA1DJ,CA4DI,0EACE,cAAA,CACA,eAAA,CACA,iBA1DN,CEpCI,yBF2FA,0EAMI,cAAA,CACA,kBAzDN,CACF,CA4DI,0EACE,aAAA,CACA,cAAA,CACA,eAAA,CACA,kBA1DN,CEhDI,yBFsGA,0EAOI,cAAA,CACA,eAzDN,CAmCA,kDA2BI,YAzDJ,CAJF","sourcesContent":["\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n\n@import \"resources/scss/user/variables\";\n.product-card {\n  height: 100%;\n  margin: 0;\n  padding: 0;\n  border: 1px solid #DEDEDE;\n  border-radius: 12px;\n  background: #ffffff;\n  transition: .25s box-shadow, .3s border-color;\n  position: relative;\n\n  &:hover {\n    box-shadow: -1px 12px 14px #D3D4DA;\n    border-color: transparent;\n  }\n\n  .badge {\n    position: absolute;\n    background-color: $color-orange;\n    color: $color-dark;\n    font-weight: 400;\n\n    border-radius: 4px;\n    padding: 2px 4px;\n    top: 8px;\n    right: 8px;\n    font-size: 10px;\n    line-height: 12px;\n  }\n\n  .product-card-image {\n    background: #ffffff;\n    padding: 0;\n    margin: 0;\n    height: 114px;\n    border-radius: 12px 12px 0 0;\n\n    img {\n      padding: 0;\n      margin: 0;\n      width: 100%;\n      height: 100%;\n      object-fit: cover;\n      object-position: center;\n      border-radius: 12px 12px 0 0;\n    }\n  }\n\n  @include respond-to(md) {\n    .badge {\n      top: 15px;\n      right: 17px;\n      border-radius: 8px;\n      font-size: 18px;\n      line-height: 21px;\n      padding: 4px 8px;\n    }\n\n    .product-card-image {\n      height: 225px;\n    }\n  }\n\n  .product-card-info {\n    padding: 8px;\n\n    .product-card-info-name {\n      font-size: 12px;\n      font-weight: 400;\n      margin-bottom: 8px;\n\n      @include respond-to(md) {\n        font-size: 16px;\n        margin-bottom: 24px;\n      }\n    }\n\n    .product-card-info-cost {\n      display: block;\n      font-size: 12px;\n      font-weight: 500;\n      margin-bottom: 12px;\n\n      @include respond-to(md) {\n        font-size: 16px;\n        margin-bottom: 0;\n      }\n    }\n\n    @include respond-to(md) {\n      padding: 16px;\n    }\n  }\n}\n\n","$color-orange: #F4B12F;\n$color-dark: #1A1819;\n$color-gray: #767676;\n$color-light-gray: #F1F1F1;","@import 'colors';\n\n$container-max-widths: (\n        sm: 100%,\n        md: 740px,\n        lg: 1080px,\n        xl: 1280px,\n);\n$grid-breakpoints: (\n        xs: 0,\n        sm: 576px,\n        md: 767px,\n        lg: 1200px,\n        xl: 1365px,\n);\n\n$font-family-base: 'Rubik', sans-serif;\n\n/// Mixin to manage responsive breakpoints\n/// @author Fulliton\n/// @param {String} $breakpoint - Breakpoint name\n/// @require $breakpoints\n@mixin respond-to($breakpoint) {\n  // If the key exists in the map\n  @if map-has-key($grid-breakpoints, $breakpoint) {\n    // Prints a media query based on the value\n    @media (min-width: map-get($grid-breakpoints, $breakpoint)) {\n      @content;\n    }\n  }\n\n    // If the key doesn't exist in the map\n  @else {\n    @warn \"Unfortunately, no value could be retrieved from `#{$breakpoint}`. \"\n        + \"Available breakpoints are: #{map-keys($grid-breakpoints)}.\";\n  }\n}\n\n@mixin respond-up($breakpoint) {\n  // If the key exists in the map\n  @if map-has-key($grid-breakpoints, $breakpoint) {\n    // Prints a media query based on the value\n    @media (max-width: map-get($grid-breakpoints, $breakpoint)) {\n      @content;\n    }\n  }\n\n    // If the key doesn't exist in the map\n  @else {\n    @warn \"Unfortunately, no value could be retrieved from `#{$breakpoint}`. \"\n        + \"Available breakpoints are: #{map-keys($grid-breakpoints)}.\";\n  }\n}"],"sourceRoot":""}]);
